'use strict';

import { ToolbarNewInfo } from '../toolbar/toolbar-new-info';
import { ns } from '../constant/namespace';

let isPanelBlurred = true;

function setPanelBlurred(blurred: boolean): void {
  isPanelBlurred = blurred;
}

/**
 * Update toolbar by the new information.
 * @param newInfo New information for toolbar to show.
 * @param force  Whether force update the new msg count even panel is blurred.
 */
function updateToolbar(newInfo: ToolbarNewInfo, force?: boolean) {
  if (!isPanelBlurred && !force && newInfo.newMsgCount !== undefined) {
    newInfo.newMsgCount = undefined;
  }
  Editor.Message.broadcast(`${ns}:update-toolbar`, newInfo);
}

export { setPanelBlurred, updateToolbar };
